import baseRouter from "./base-router";
import BaseLayout from "@/view/layout/base-layout";
const routes = [
  {
    path: "/",
    name: "base-index",
    redirect: "/index",
    component: BaseLayout,
    children: baseRouter
  },
  {
    path: "/login",
    name: "login",
    component: () => import("@/view/account/login")
  },
  {
    path: "/pay",
    name: "pay",
    component: () => import("@/view/order/pay")
  },
  {
    redirect: "/404",
    path: "/:catchAll(.*)"
  }
];

export default routes
