import { post, get, put, _delete } from "@/lin/plugin/axios";

class Article {
  // constructor() {}

  async addArticle(info) {
    const res = await post("api/blog/articles/", info);
    return res;
  }

  async getArticle(id) {
    const res = await get(`api/blog/articles/${id}`);
    return res;
  }
  async getArticleAi(id,type) {
    const res = await get(`api/blog/articles/summary/${id}/${type}`);
    return res;
  }
  async postProduct(queryinfo) {
    const res = await post(`api/blog/order/getproduct/${queryinfo}`);
    return res;
  }
  async postOrderQuery(query) {
    const res = await post(`api/blog/order/queryorder/${query}`);
    return res;
  }
  async postOrderNo() {
    const res = await post(`api/blog/order/create`);
    return res;
  }
  async postOrderNo2(type) {
    const res = await post(`api/blog/order/createvip/${type}`);
    return res;
  }
  async postProductOrderNo(amount,orderno) {
    const res = await post(`api/blog/order/createproduct/${amount}/${orderno}`);
    return res;
  }
  async postOrderNoPay(id) {
    const res = await post(`api/blog/order/pay/${id}`);
    return res;
  }
  async postSaveEmail(id,email) {
    const res = await post(`api/blog/order/saveemail/${id}/${email}`);
    return res;
  }
  async postOrderNoPayProduct(id) {
    const res = await post(`api/blog/order/payproduct/${id}`);
    return res;
  }
  async postOrderNoPayCheck(id) {
    const res = await post(`api/blog/order/paycheck/${id}`);
    return res;
  }
  async postOrderNoPayCheck2(id) {
    const res = await post(`api/blog/order/paycheck2/${id}`);
    return res;
  }

  async postOrderNoPayResult(id) {
    const res = await post(`api/blog/order/payresult/${id}`);
    return res;
  }

  async postChatAsync(info) {
    const res = await post(`api/blog/articles/chat`,info);
    return res;
  }
  
  async editArticle(id, info) {
    const res = await put(`api/blog/articles/${id}`, info);
    return res;
  }

  async deleteArticle(id) {
    const res = await _delete(`api/blog/articles/${id}`);
    return res;
  }

  async deleteCmsArticle(id) {
    const res = await _delete(`api/blog/articles/cms/${id}`);
    return res;
  }

  async getArticles(pagesParmas) {
    const res = await get("api/blog/articles/", pagesParmas);
    return res;
  }

  async getAllArticles(pagesParmas) {
    const res = await get("api/blog/articles/all", pagesParmas);
    return res;
  }

  async getQueryArticles(pagesParmas) {
    const res = await get("api/blog/articles/query", pagesParmas);
    return res;
  }

  async getSubscribeArticles(pagesParmas) {
    const res = await get("api/blog/articles/subscribe", pagesParmas);
    return res;
  }


  async getArticleDraft(id) {
    const res = await get(`api/blog/articles/draft/${id}`);
    return res;
  }

  async editArticleDraft(id, info) {
    const res = await put(`api/blog/articles/draft/${id}`, info);
    return res;
  }
  async updateCommentable(id, commetable) {
    const res = await put(`api/blog/articles/${id}/comment-able/${commetable}`);
    return res;
  }
}

export default new Article();
