import articleApi from '../../model/article';
export default {
  name: 'OrderQueryDialog',
  data() {
    return {
      dialogVisible: false,
      orderQuery: '',
      orders: [],
      order: null
    };
  },
  methods: {
    show() {
      this.dialogVisible = true;
    },
    hide() {
      this.dialogVisible = false;
      this.orderQuery = '';
      this.orders = [];
      this.order = null;
    },
    async handleOrderSearch() {
      if (!this.orderQuery) {
        this.order = null;
        this.orders = [];
        return;
      }
      try {
        const orderResponse = await articleApi.postOrderQuery(this.orderQuery);
        if (orderResponse && orderResponse.length > 0) {
          this.orders = orderResponse;
          this.order = orderResponse[0];
        } else {
          this.orders = [];
          this.order = null;
        }
      } catch (error) {
        console.error('查询订单失败', error);
        this.orders = [];
        this.order = null;
      }
    }
  }
};